    import {
        mapState,
        mapMutations
    } from 'vuex'

    export default {
        computed: {
            ...mapState(['hasLogin', 'forcedLogin'])
        },
		data() {
			return {
				menuBorder: false,
				menuArrow: true,
				menuCard: true,
				// skin: false,
				modalName: null,
				// pfskin: false,
				// music: false,
				// adskin: false,
				// about_autor: '关于作者',
				// article: [],
				avatarUrl:'',
				nickName:'c',
				logined:false,
				showloginout:false,
			}
		},
        methods: {
            ...mapMutations(['logout']),
            bindLogin() {
                uni.navigateTo({
                    url: '../login/login',
                });
            },
            bindLogout() {
                this.logout();
                /**
                 * 如果需要强制登录跳转回登录页面
                 */
                if (this.forcedLogin) {
                    uni.reLaunch({
                        url: '../login/login',
                    });
                }
            },
			showModal(e) {
				this.modalName = e.currentTarget.dataset.target
			},
			hideModal(e) {
				this.modalName = null
			},
			toChild(e) {
				uni.navigateTo({
					url: e.currentTarget.dataset.url
				})
			},
        }
    }
